import useSWR from "swr"
import { createClient } from "@/lib/supabase/client"
import type { Staff, DailyRecord } from "@/lib/types"

export function useStaff() {
  return useSWR<Staff[]>("/api/staff", fetchStaff)
}

export function useDailyRecords(params?: Record<string, any>) {
  // params can be used for filtering (date, staff, etc.)
  return useSWR(["/api/daily_records", params], ([, p]) => fetchDailyRecords(p))
}

async function fetchStaff(): Promise<Staff[]> {
  const supabase = createClient()
  const { data, error } = await supabase.from("staff").select("*")
  if (error) throw error
  return data || []
}

async function fetchDailyRecords(params?: Record<string, any>): Promise<DailyRecord[]> {
  const supabase = createClient()
  let query = supabase.from("daily_records").select("*, staff(name)")
  if (params) {
    if (params.record_date) query = query.eq("record_date", params.record_date)
    if (params.start_date && params.end_date) query = query.gte("record_date", params.start_date).lte("record_date", params.end_date)
    if (params.staff_id) query = query.eq("staff_id", params.staff_id)
  }
  const { data, error } = await query
  if (error) throw error
  return data || []
}
